<template>
    <d2-container>
        <!-- 订单状态管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="订单状态管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <el-button type="primary" plain>查询</el-button>
                    <!-- <el-button type="primary" plain @click="add_good">添加</el-button> -->
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="order_number" label="订单编号">
            </el-table-column>
            <el-table-column prop="sum_price" label="订单总价--单位￥">
            </el-table-column>
            <el-table-column prop="order_status" label="订单状态">
            </el-table-column>
            <el-table-column prop="distribution_way" label="配送方式">
            </el-table-column>
            <el-table-column prop="distribution_fee" label="配送费用--单位￥">
            </el-table-column>
            <el-table-column prop="pay_way" label="付款方式">
            </el-table-column>
            <el-table-column prop="pay_status" label="付款状态">
            </el-table-column>
            <el-table-column prop="use_red_packet" label="是否使用优惠卷">
            </el-table-column>
            <el-table-column prop="red_packet_price" label="优惠金额--单位￥">
            </el-table-column>
            <el-table-column prop="real_pay_price" label="实付金额--单位￥">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_type(val.row)">修改订单状态</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button>   -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加订单状态管理 -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="100px">
                <el-form-item label="修改订单状态">
                    <el-input v-model="form.order_status" placeholder="1=>待发货，2=> 待收货, 3=>已收货"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit_type">确 定</el-button>
            </span>
        </el-dialog>

    </d2-container>

</template>

<script>
    import { add_goods_type, show_goods_type, edit_goods_type, del_goods_type } from '../../plugin/axios/api.js';
    import { show_order, edit_order, del_order } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'GoodStyle_home',
        data() {
            return {
                num: '修改订单状态',
                value3: '',
                state1: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    order_status: '',
                },
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            add_good() {//添加商品
                // this.num = "添加商品类别管理";
                this.centerDialogVisible = true;
            },
            onSubmit() {//添加商品
                let par = qs.stringify(this.form)
            },
            edit_type(a) {//编辑商品
                // this.num = "编辑商品类别管理";
                this.form = {
                    order_status:'',
                    id: a.id
                }
                this.centerDialogVisible = true;
            },
            onSubmit_type() {//编辑商品
                let par = {
                    order_status: this.form.order_status,
                }
                let id = this.form.id;
                edit_order(par, id).then(res => {
                    this.msg(res)
                })
            },
            del_val(a) {//删除商品
                console.log(a);
                del_order(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {
                show_order(this.pages).then(res => {
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()
                    return
                }
                Myerror(res, this)
            },
            clear() {
                this.centerDialogVisible = false;
                this.form = {
                    order_status: '',
                }

            }
        },
        mounted() {
            this.init_goods()
        }
    }
</script>
<style lang="scss">
</style>